.profile-modal {
  background-color: rgba(255, 255, 255, 0.8);
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.modal-close {
  font-size: 3rem;
  font-style: bold;
  cursor: pointer;
}

.modal-close:hover {
  color: grey;
  cursor: pointer;
}

.modal {
  opacity: 1;
  -webkit-transition: opacity 300ms ease-in-out;
  -o-transition: opacity 300ms ease-in-out;
  transition: opacity 300ms ease-in-out;
}

.is-horizontal-center {
  margin: auto;
}


